<template>
  <div id="view-book">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>{{author}}</h4>
      </li>
      <li class="collection-item">Book ID#: {{book_id}}</li>
      <li class="collection-item">Author: {{author}}</li>
      <li class="collection-item">Title: {{title}}</li>
      <li class="collection-item">Published: {{published}}</li>
    </ul>

    <router-link to="/" class="btn grey">Back</router-link>
    <button @click="deleteBook" class="btn red">Delete</button>

    <div class="fixed-action-btn">
      <router-link
        v-bind:to="{name: 'edit-book', params: {book_id: book_id}}"
        class="btn-floating btn-large pink"
      >
        <i class="fa fa-pencil"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import db from "./firebaseInit";
export default {
  name: "view-book",
  data() {
    return {
      book_id: null,
      author: null,
      title: null,
      published: null,
    };
  },
  beforeRouteEnter(to, from, next) {
    db.collection("books")
      .where("book_id", "==", to.params.book_id)
      .get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          next((vm) => {
            vm.book_id = doc.data().book_id;
            vm.author = doc.data().author;
            vm.title = doc.data().title;
            vm.published = doc.data().published;
          });
        });
      });
  },
  watch: {
    $route: "fetchData",
  },
  methods: {
    fetchData() {
      db.collection("books")
        .where("book_id", "==", this.$route.params.book_id)
        .get()
        .then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            this.book_id = doc.data().book_id;
            this.author = doc.data().author;
            this.title = doc.data().title;
            this.published = doc.data().published;
          });
        });
    },
    deleteBook() {
      if (confirm("...are you sure?")) {
        db.collection("books")
          .where("book_id", "==", this.$route.params.book_id)
          .get()
          .then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
              doc.ref.delete();
              this.$router.push("/");
            });
          });
      }
    },
  },
};
</script>


