<template>
  <div id="edit-book">
    <h3>Edit Book</h3>

    <div class="row">
      <form @submit.prevent="updateBook" class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input disabled type="text" v-model="book_id" required />
          </div>
        </div>

        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="author" required />
          </div>
        </div>

        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="title" required />
          </div>
        </div>

        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="published" required />
          </div>
        </div>

        <button type="submit" class="btn">Submit</button>
        <router-link to="/" class="btn grey">Cancel</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import db from "./firebaseInit";
export default {
  name: "edit-book",
  data() {
    return {
      book_id: null,
      author: null,
      title: null,
      published: null,
    };
  },
  beforeRouteEnter(to, from, next) {
    db.collection("books")
      .where("book_id", "==", to.params.book_id)
      .get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          next((vm) => {
            vm.book_id = doc.data().book_id;
            vm.author = doc.data().author;
            vm.title = doc.data().title;
            vm.published = doc.data().published;
          });
        });
      });
  },
  watch: {
    $route: "fetchData",
  },
  methods: {
    fetchData() {
      db.collection("books")
        .where("book_id", "==", this.$route.params.book_id)
        .get()
        .then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            this.book_id = doc.data().book_id;
            this.author = doc.data().author;
            this.title = doc.data().title;
            this.published = doc.data().published;
          });
        });
    },
    updateBook() {
      db.collection("books")
        .where("book_id", "==", this.$route.params.book_id)
        .get()
        .then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            doc.ref
              .update({
                book_id: this.book_id,
                author: this.author,
                title: this.title,
                published: this.published,
              })
              .then(() => {
                this.$router.push({
                  name: "view-book",
                  params: { book_id: this.book_id },
                });
              });
          });
        });
    },
  },
};
</script>


