<template>
  <div id="dashboard">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>Books</h4>
      </li>

      <li v-for="book in books" v-bind:key="book.id" class="collection-item">
        <div class="chip">{{book.author}}</div>

        {{book.book_id}}: {{book.title}}
        <router-link
          class="secondary-content"
          v-bind:to="{name: 'view-book', params: {book_id: book.book_id}}"
        >
          <i class="fa fa-eye"></i>
        </router-link>
      </li>
    </ul>

    <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-large pink">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import db from "./firebaseInit";
export default {
  name: "dashboard",
  data() {
    return {
      books: [],
    };
  },
  created() {
    db.collection("books")
      .orderBy("author")
      .get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          const data = {
            id: doc.id,
            book_id: doc.data().book_id,
            author: doc.data().author,
            title: doc.data().title,
            published: doc.data().published,
          };
          this.books.push(data);
        });
      });
  },
};
</script>


